body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Landing page style */
.mdl-layout{
  position: relative !important;
}
.header-bg{
  background: #833ab4;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #fcb045, #fd1d1d, #833ab4);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(right, #fcb045, #fd1d1d, #833ab4);
  background: linear-gradient(to left, #fcb045, #fd1d1d, #833ab4); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  z-index: 1000;
  position: fixed;
}
header.header-bg a:hover{
  border-top:10px solid red;
}
header.header-bg .mdl-layout-title a:hover{
  border-top:0;
}
.footer-section{
  background: #222 !important;
  color: #ddd !important;
}
.footer-section a:hover{
  color: #888;
}
.mdl-layout__drawer-button{
  position: fixed !important;
}
.page-content{
  margin-top: 5em;
}
.avatar-img{
  height: 220px;
  border-radius: 50%;
  border: 5px solid #d4d4d4;
}
.banner-grid{
  text-align: center;
  width: 100%;
  margin-bottom: 50px !important;
}
.banner-text{
  background-color: #000;
  opacity: 8;
  width: 75%;
  margin: auto;
  border-radius: 10px;
}
.banner-text h1{
  font-size: 66px;
  font-weight: bold;
  color: white;
  /*text-align: center;*/
}
.banner-text hr{
  border-top:5px dotted white;
  width: 50%;
  margin: auto;
}
.banner-text p{
  color: #a6a6a6;
  /*text-align: center;*/
  font-size: 18px;
  padding: 20px;
}
.social-links{
  display: flex;
  justify-content: space-between;
  width: 50%;
  margin: auto;
}
.social-links i{
  color: white;
  font-size: 5em;
  padding-bottom: 18px;
}

/* END Landing page style */


/* Contact page css */
.contact-body{
  margin: auto;
  padding: 100px 0;
  width: 100%;
  justify-content: center;
}
.contact-grid{
  text-align: center;
  justify-content: center;;
  width: 80%;
  background: white;
  color: black;
}
.contact-grid h2{
  font-family: Anton;
}
.contact-grid hr{
  border-top:5px dotted black;
  width: 50%;
  margin:auto;
}
.contact-list i{
  font-size: 45px;
  padding-right: 3rem;
}


/* Resume page css */
.resume-grid{
  width: 100%;
}
.resume-left-col{
  padding-left: 60px;
}
.resume-right-col{
  background: #424242;
  color: #fff;
  padding: 2em;
}
/* END Resume page css */


/* Project page css */
.projects-grid{
  display: flex;
  justify-content: center;
  margin-top: 50px;
  margin-bottom: 60px;
}
.projects-grid .mdl-card{
  margin-right: 40px !important;
  margin-left: 0 !important;
  min-width: 180px;
}
.projects-grid .mdl-card:last-of-type{
  margin-right: 0 !important;
}
.mdl-card__actions{
  padding: 20px !important;
  text-align: center;
}
.mdl-card__actions a{
  text-decoration: none;
  color: #fff;
  font-size: 14px;
  padding: 12px 30px;
  background-color: #E48429;
  border-radius: 10px;
}
.mdl-card__actions a:hover{
  background-color: #EB9E56;
}
/* END Project page css */


/* fixes */
.mdl-layout__drawer-button {
	z-index: 9000;
}

